// code taken from: http://www.script-tutorials.com/css3-modal-popups/

.lightbox-overlay {
    background-color: rgba(0, 0, 0, 0.8);
    bottom: 0;
    cursor: default;
    left: 0;
    opacity: 0;
    position: absolute;
    right: 0;
    top: 0;
    visibility: hidden;
    z-index: 8;
    height: 100%;
    width: 100%;
    margin: 0px;

    -webkit-transition: opacity .5s;
    -moz-transition: opacity .5s;
    -ms-transition: opacity .5s;
    -o-transition: opacity .5s;
    transition: opacity .5s;
}

.lightbox-overlay:target {
    visibility: visible;
    opacity: 1;
    display: flex;
    flex-direction: column;
}

.lightbox-popup {
    left: 0;
    border: 0;
    opacity: 0;
    padding: 0;
    position: absolute;
    text-align: justify;
    top: 0;
    visibility: hidden;
    z-index: 10;
    height: 100vh;
    width: 100vw;

}


.lightbox-popup p, .lightbox-popup div {
    margin-bottom: 0;
}

.lightbox-overlay:target+.lightbox-popup {
    opacity: 1;
    visibility: visible;
    margin: 1em;
}

.lightbox-popup .masonry-panel {
    display: inline-block;
    vertical-align: middle;}

.lightbox-image-div {
    position: relative;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    z-index: 10;
    display: table;
}
.lightbox-image {
    position: absolute;
    max-height: 90vh;
    max-width: 80vw;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.parent {
    position: relative;
  }
  .child {

  }

.lightbox-nav {
    padding-top: 30px;
    padding-bottom: 30px;

}
.lightbox-image-description {
    position: fixed;
    left: 0;
    bottom: 80px;
    width: 100%;
    z-index: 99;
    padding: 10px;
    background: $lightbox-description-background-color;
    color: $lightbox-description-text-color;
    font-size: 1em;
    opacity: 1;
    text-align: center;
    transition: opacity ease-in-out 0.5s;
	margin: auto;
    max-height:100%;
    padding-left: 50px;
    padding-right: 50px;
}

.lightbox-close-description {
    color: $lightbox-navigation-color;
    position: fixed;
    right: 25px;
    z-index: 999;
    padding: 3px 3px 10px 10px;
    font-size: 24px;
    text-decoration: none;
    line-height: 20px;
}

p:focus {
	opacity: 0;
    visibility: invisible;
    outline: 0;
}

p:focus ~ .lightbox-image-description {
	opacity: 0;
    visibility: invisible;
    outline: 0;
}

.lightbox-caption {
    color: $lightbox-caption-text-color;
}

////////////////////////////////////////////////////


.lightbox-popup-close {
    position: fixed;
    right: 60px;
    width: 30px;
    top: 0;
    z-index: 11;
}

.lightbox-prev {
    position: fixed;
    left: 60px;
    width: 100px;
    top: 50%;
    height: 100px;
    z-index: 11;

}

.lightbox-next {
    position: fixed;
    width: 100px;
    height: 100px;
    top: 50%;
    right: 60px;
    z-index: 11;
}


a.lightbox-prev, a.lightbox-next, a.lightbox-popup-close {
    color: $lightbox-navigation-color;
    font-size: 5em;
    -webkit-font-smoothing: antialiased;
}

a.lightbox-prev:visited, a.lightbox-next:visited, a.lightbox-popup-close:visited {
    color: $lightbox-navigation-color;
    font-size: 5em;
    -webkit-font-smoothing: antialiased;
}

.zoom {
  transition: all 0.2s linear;
  -webkit-transition: all 0.2s linear;
  -moz-transition: all 0.2s linear;
  -o-transition: all 0.2s linear;
}

.zoom:hover {
  -moz-transform: scale(1.05);
  -webkit-transform: scale(1.05);
  transform: scale(1.05);
  transition: all 0.2s linear;
  -webkit-transition: all 0.2s linear;
  -moz-transition: all 0.2s linear;
  -o-transition: all 0.2s linear;
  overflow: auto;
}

